import React, { Component } from 'react';
import { Container, Button, Form } from 'react-bootstrap';
import { changePin } from '../store/auth/actions';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import history from '../services/history';

class ResetPasswordNew extends Component {
  state = {
    newPassword: '',
    confirmPassword: '',
    validated: false,
    loading: true,
  };

  constructor(props) {
    super(props);
    this.handleNewPassword = this.handleNewPassword.bind(this);
    this.handleConfirmPassword = this.handleConfirmPassword.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleNewPassword(event) {
    this.setState({
      newPassword: event.target.value,
    });
  }

  handleConfirmPassword(event) {
    this.setState({
      confirmPassword: event.target.value,
    });
  }

  handleSubmit(e) {
    e.preventDefault();
    const form = e.currentTarget;
    if (form.checkValidity() === false) {
      this.setState({ validated: true });
    } else if (this.state.newPassword === this.state.confirmPassword) {
      history.push('/password-success');
    } else {
      console.log('password dont match');
      this.setState({ validated: true });
    }
  }

  render() {
    let saveButton;
    // Reset password mutation to be added
    if (this.state.loading) {
      saveButton = (
        <Button
          variant="primary"
          type="submit"
          className="d-flex align-items-center justify-content-around"
        >
          <span className="text-uppercase">Save</span>
          <img src={require('../assets/images/arrow.svg')} alt="" />
        </Button>
      );
    } else {
      saveButton = (
        <Button variant="secondary">
          <div className="spinner-border" role="status">
            <span className="sr-only">Loading...</span>
          </div>
        </Button>
      );
    }

    return (
      <div className="reset-pwd-container">
        <Container>
          <div className="reset-pwd-content align-items-baseline">
            <strong>
              <font size="5" face="NeutraTextTF-DemiAlt">
                Reset Password
              </font>
            </strong>
            <p>
              Choose a strong password and don&apos;t reuse it for other
              accounts.
            </p>
            <Form
              noValidate
              validated={this.state.validated}
              onSubmit={this.handleSubmit}
            >
              <Form.Group controlId="formNewPassword">
                <Form.Control
                  required
                  type="password"
                  placeholder="New Password"
                  value={this.state.newPassword}
                  onChange={this.handleNewPassword}
                />
                <Form.Control.Feedback type="invalid">
                  Please choose a password
                </Form.Control.Feedback>
              </Form.Group>
              <Form.Group controlId="formConfirmPassword">
                <Form.Control
                  required
                  type="password"
                  placeholder="Confirm Password"
                  value={this.state.confirmPassword}
                  onChange={this.handleConfirmPassword}
                  {...(this.state.newPassword === this.state.confirmPassword
                    ? {}
                    : { isInvalid: true })}
                />
                <Form.Control.Feedback type="invalid">
                  Please enter confirm password
                </Form.Control.Feedback>
              </Form.Group>
              {saveButton}
            </Form>{' '}
            <br />
            <br />
            <strong>Password strength:</strong>
            <p>
              Use atleast 8 characters. Don&apos;t use a password from another
              site, or something too obvious like your pet&apos;s name.{' '}
              <span style={{ color: '#1C9A5B' }} className="pointer">
                Why?
              </span>
            </p>
          </div>
        </Container>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    showSpinner: state.util.showSpinner,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    changePin: (data) => {
      dispatch(changePin(data));
    },
  };
};

ResetPasswordNew.propTypes = {
  changePin: PropTypes.func,
  showSpinner: PropTypes.bool,
};

export default connect(mapStateToProps, mapDispatchToProps)(ResetPasswordNew);
