import React, { useContext } from 'react';
import { Container, Navbar, Nav } from 'react-bootstrap';
import './index.scss';
import Dropdown from 'react-bootstrap/Dropdown';
import history from '../../services/history';
import { useQuery } from '@apollo/client';
import { GET_OFFICER_PROFILE } from '../../graphql/query/officerProfile';
import { removeLoginState } from '../../graphql/cache';
import { AppContext } from './../../AppContext';

const Header = () => {
  const { permissionLevel, setPermissionLevel } = useContext(AppContext);

  const { data, client, loading } = useQuery(GET_OFFICER_PROFILE);

  const loadUserInfo = () => {
    if (loading) return <a>Loading...</a>;
    return (
      <div className="user-details">
        <p>{data.Officer?.firstName + ' ' + data.Officer?.lastName}</p>
        <span>{data.Officer?.position}</span>
        <span>{permissionLevel}</span>
      </div>
    );
  };

  const logoutUser = () => {
    removeLoginState(client);
    history.push('/login');
  };

  return (
    <Navbar fixed="top">
      <Container>
        <Navbar.Brand
          style={{ cursor: 'pointer' }}
          onClick={() => {
            history.push('/');
          }}
        >
          <div className="d-flex align-items-center">
            <img
              src={require('../../assets/images/logo.png')}
              width="56"
              alt="logo here"
            />
            <h1>Ts-BPass</h1>
          </div>
        </Navbar.Brand>

        <Nav className="ml-auto">
          <Nav.Link>
            <img
              src={require('../../assets/images/bell-icon.svg')}
              alt="bellIcon"
            />{' '}
            <span className="badge badge-warning" />
          </Nav.Link>
          <Dropdown>
            <Dropdown.Toggle>
              <div className="d-flex align-items-center">
                <img
                  src={require('../../assets/images/user.png')}
                  className="rounded-circle"
                  alt="user"
                />

                {loadUserInfo()}
              </div>
            </Dropdown.Toggle>

            <Dropdown.Menu>
              <Dropdown.Item href="#/action-1">View Profile</Dropdown.Item>
              <Dropdown.Divider />
              <Dropdown.Item
                onClick={() => {
                  history.push('/reset-password');
                }}
              >
                <img
                  src={require('../../assets/images/change-password.svg')}
                  alt="change-password"
                />
                Change Password
              </Dropdown.Item>
              <Dropdown.Divider />
              <Dropdown.Item
                onClick={() => {
                  logoutUser();
                }}
              >
                <img
                  src={require('../../assets/images/logout.svg')}
                  alt="logout"
                />
                Logout
              </Dropdown.Item>
              {/* adding temporary permission level change */}
              <Dropdown.Divider />
              <Dropdown.Item
                onClick={() => {
                  setPermissionLevel('technical-verification');
                }}
              >
                Techincal Verification Officer
              </Dropdown.Item>
              <Dropdown.Divider />
              <Dropdown.Item
                onClick={() => {
                  setPermissionLevel('tpso-officer');
                }}
              >
                TPSO officer
              </Dropdown.Item>
              <Dropdown.Divider />
              <Dropdown.Item
                onClick={() => {
                  setPermissionLevel('acp-officer');
                }}
              >
                ACP officer
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </Nav>
      </Container>
    </Navbar>
  );
};

export default Header;
