import React, { Component } from 'react';
import AuthLayout from '../components/layouts/AuthLayout';
import { connect } from 'react-redux';
import { Form, Button, Row, Col } from 'react-bootstrap';
import { resetPin } from '../store/auth/actions';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import history from '../services/history';

class ResetPassword extends Component {
  state = {
    mobileNumber: '',
    validated: false,
    loading: true,
  };

  constructor(props) {
    super(props);
    this.handleChangeMobileNumber = this.handleChangeMobileNumber.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChangeMobileNumber(event) {
    this.setState({ mobileNumber: event.target.value });
  }
  handleSubmit(e) {
    e.preventDefault();
    const form = e.currentTarget;
    if (form.checkValidity() === false) {
      this.setState({ validated: true });
    } else {
      let inputData = {
        officer: {
          mobile: this.state.mobileNumber,
        },
      };
      this.props.resetPin(inputData);
      history.push('/change-password-OTP');
    }
  }

  render() {
    let resetButton;
    // Reset password mutation to be added
    if (this.state.loading) {
      resetButton = (
        <Button
          variant="primary"
          type="submit"
          className="d-flex align-items-center justify-content-between"
        >
          <span className="text-uppercase">Reset Password</span>
          <img src={require('../assets/images/arrow.svg')} alt="" />
        </Button>
      );
    } else {
      resetButton = (
        <Button variant="secondary">
          <div className="spinner-border" role="status">
            <span className="sr-only">Loading...</span>
          </div>
        </Button>
      );
    }

    return (
      <AuthLayout>
        <h2>Officer&apos;s Password Change</h2>
        <Form
          noValidate
          validated={this.state.validated}
          onSubmit={this.handleSubmit}
        >
          <Form.Group controlId="formBasicEmail">
            <Form.Label>Mobile Number</Form.Label>
            <Form.Control
              required
              type="number"
              placeholder="Enter mobile number"
              value={this.state.mobileNumber}
              onChange={this.handleChangeMobileNumber}
            />
            <Form.Control.Feedback type="invalid">
              Please enter the mobile number
            </Form.Control.Feedback>
          </Form.Group>
          <Row className={' align-items-center '}>
            <Col>{resetButton}</Col>
            <Col>
              <p className="forgot-password text-right">
                <Link to="/login">Go back</Link>
              </p>
            </Col>
          </Row>
        </Form>
      </AuthLayout>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    showSpinner: state.util.showSpinner,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    resetPin: (data) => {
      dispatch(resetPin(data));
    },
  };
};

ResetPassword.propTypes = {
  resetPin: PropTypes.func,
};

export default connect(mapStateToProps, mapDispatchToProps)(ResetPassword);
