/* eslint-disable one-var */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ApplicationItem from './ApplicationItem';
import './index.scss';
import { Nav, Form, Tab, Table } from 'react-bootstrap';

const mapStatusToHeader = {
  Accepted: 'green',
  Rejected: 'red',
  'In Progress': 'orange',
  'In Hold': 'deemed',
};

function checkFilter(item, filter) {
  if (filter === 'all') return true;
  if (mapStatusToHeader[item.status] === filter) return true;
  return false;
}
class ApplicationList extends Component {
  constructor(props) {
    super(props);
    let count = {
      green: 0,
      red: 0,
      orange: 0,
      deemed: 0,
      all: 0,
    };
    for (const item in props.data) {
      count[mapStatusToHeader[props.data[item].status]]++;
      count.all++;
    }
    this.state = {
      filter: 'all',
      count,
    };
  }

  changeFilter = (color) => {
    this.setState((state) => {
      return {
        filter: color,
      };
    });
  };
  // console.log(props.data);
  render() {
    return (
      <div className="al-container">
        <Tab.Container id="left-tabs-example" defaultActiveKey="all">
          <div className="al-header">
            <Nav variant="tabs">
              <Nav.Item className="d-flex align-item-center">
                <Nav.Link eventKey="all" onSelect={this.changeFilter}>
                  All <span>({this.state.count.all})</span>
                </Nav.Link>

                <Nav.Link eventKey="green" onSelect={this.changeFilter}>
                  Green{' '}
                  <span className="badge green">{this.state.count.green}</span>
                </Nav.Link>

                <Nav.Link eventKey="orange" onSelect={this.changeFilter}>
                  Orange{' '}
                  <span className="badge orange">
                    {this.state.count.orange}
                  </span>
                </Nav.Link>

                <Nav.Link eventKey="deemed" onSelect={this.changeFilter}>
                  Deemed{' '}
                  <span className="badge deemed">
                    {this.state.count.deemed}
                  </span>
                </Nav.Link>

                <Nav.Link eventKey="red" onSelect={this.changeFilter}>
                  Red <span className="badge red">{this.state.count.red}</span>
                </Nav.Link>
              </Nav.Item>
            </Nav>
            <div className="d-flex align-items-center">
              <div className="search-container">
                <Form.Group controlId="formBasicEmail">
                  <Form.Control type="text" placeholder="Search" />
                </Form.Group>
              </div>
              <div className="filter-container">
                <img
                  src={require('../../assets/images/equalizer.svg')}
                  alt="equalizer"
                />
                <p>Filter</p>
              </div>
            </div>
          </div>
          <div className="al-content">
            <Tab.Content>
              <div>
                <Table striped hover>
                  <thead>
                    <tr>
                      <th className="pr-0" />
                      <th className="pl-0">Application Id</th>
                      <th>Type</th>
                      <th>Address</th>
                      <th>Area Floors</th>
                      <th>Due Date</th>
                      <th>Status</th>
                    </tr>
                  </thead>
                  <tbody>
                    {this.props.data &&
                      this.props.data.map((item) => {
                        if (checkFilter(item, this.state.filter)) {
                          return (
                            <ApplicationItem
                              key={item.id}
                              data={item}
                              pageType={this.props.pageType}
                            />
                          );
                        }
                        return null;
                      })}
                  </tbody>
                </Table>
              </div>
            </Tab.Content>
          </div>
        </Tab.Container>
      </div>
    );
  }
}

ApplicationList.propTypes = {
  data: PropTypes.array,
  pageType: PropTypes.string,
};

export default ApplicationList;
