import React, { Component } from 'react';
import AuthLayout from '../components/layouts/AuthLayout';
import { Form, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { Mutation } from '@apollo/client/react/components';
import history from '../services/history';
import { LOGIN_OFFICER } from '../graphql/mutatution/loginOfficer';
import { saveLoginState } from '../graphql/cache';

class Login extends Component {
  state = {
    username: '',
    password: '',
    validated: false,
    isShowPassword: false,
  };

  constructor(props) {
    super(props);
    this.handleChangeUserName = this.handleChangeUserName.bind(this);
    this.handleChangePassword = this.handleChangePassword.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.toggleShowPassword = this.toggleShowPassword.bind(this);
  }

  handleSubmit(e, mutation, client) {
    e.preventDefault();
    const form = e.currentTarget;
    if (form.checkValidity() === false) {
      this.setState({ validated: true });
    } else {
      let loginInput = {
        officer: {
          mobile: this.state.username,
          password: this.state.password,
        },
      };

      mutation({
        variables: {
          mobile: loginInput.officer.mobile,
          password: loginInput.officer.password,
        },
      })
        .then((data) => {
          saveLoginState(client, data.data.login);
          history.push('/post-verification');
        })
        .catch((e) => console.log(e));
    }
  }

  toggleShowPassword() {
    this.setState({ isShowPassword: !this.state.isShowPassword });
  }
  handleChangeUserName(event) {
    this.setState({ username: event.target.value });
  }

  handleChangePassword(event) {
    this.setState({ password: event.target.value });
  }
  render() {
    const formText = {
      fontFamily: 'Neutra Text',
      fontSize: 18,
    };
    return (
      <Mutation mutation={LOGIN_OFFICER}>
        {(mutation, { loading, client, error }) => {
          let submitButton;
          if (!loading) {
            submitButton = (
              <Button
                variant="secondary"
                type="submit"
                className="d-flex align-items-center justify-content-between"
              >
                <span className="text-uppercase">Login</span>
                <img src={require('../assets/images/arrow.svg')} alt="" />
              </Button>
            );
          } else {
            submitButton = (
              <Button variant="secondary">
                <div className="spinner-border" role="status">
                  <span className="sr-only">Loading...</span>
                </div>
              </Button>
            );
          }
          return (
            <AuthLayout>
              <Form
                noValidate
                validated={this.state.validated}
                onSubmit={(e) => this.handleSubmit(e, mutation, client)}
              >
                <p style={formText} className="mb-2">
                  Officers Login{' '}
                </p>

                <Form.Group controlId="formBasicEmail">
                  <Form.Control
                    required
                    type="number"
                    value={this.state.username}
                    placeholder="Enter email address"
                    onChange={this.handleChangeUserName}
                  />
                  <Form.Control.Feedback type="invalid">
                    Please write mobile number
                  </Form.Control.Feedback>
                </Form.Group>
                <Form.Group controlId="formBasicPassword" className="password">
                  <Form.Control
                    required
                    type={this.state.isShowPassword ? 'text' : 'password'}
                    value={this.state.password}
                    placeholder="Enter password"
                    onChange={this.handleChangePassword}
                  />
                  <img
                    style={{ cursor: 'pointer' }}
                    onClick={() => this.toggleShowPassword()}
                    className="password-img form-group"
                    src={require('../assets/images/password.svg')}
                  />
                  <Form.Control.Feedback type="invalid">
                    Please choose a password
                  </Form.Control.Feedback>
                </Form.Group>
                <span>
                  {error ? <a>Username or Password incorrect Retry</a> : null}
                </span>
                <div className="loginBtnContainer">
                  {submitButton}
                  <p className="forgot-password">
                    Forgot password?
                    <Link to="/reset-password">Reset</Link>
                  </p>
                </div>
              </Form>
            </AuthLayout>
          );
        }}
      </Mutation>
    );
  }
}

export default Login;
