import apiHandler from '../../services/apiHandler';
import environment from 'environment';
import { isSuccessfullResponse } from '../../util/helperFunctions';
import Officer from './models/Officer';

export const login = async (loginInput) => {
  return new Promise((resolve, reject) => {
    const endpoint = environment.api.login;
    apiHandler
      .post(endpoint, JSON.stringify(loginInput))
      .then((response) => {
        if (isSuccessfullResponse(response)) {
          return response.data;
        } else {
          throw new Error(`API failed with status ${response.status}`);
        }
      })
      .then((json) => {
        if (json && json.data && json.success === true) {
          // response success checking logic
          resolve({
            officer: new Officer(json.data),
            access_token: json.data.access_token,
          });
        } else {
          throw new Error('Username or Password is incorrect');
        }
      })
      .catch((err) => {
        reject(new Error(`Login Failed -${err}`));
      });
  });
};

