import React, { Component } from 'react';
import { Query } from '@apollo/client/react/components';
import ErrorScreen from '../components/ErrorScreen';
import LoadingScreen from '../components/LoadingScreen';
import QuestionCards from '../components/QuestionCards';
import Header from '../components/Header';
import Sidebar from '../components/Sidebar';
import RightSideBar from '../components/RightSideBar';
import PropTypes from 'prop-types';
import { GET_OFFICERS_FORM } from '../graphql/query/officersForm';
import { GET_OFFICERS_APPLICATION } from '../graphql/query/officersApplication';

export default class TechnicalVerification extends Component {
  render() {
    return (
      <>
        <Header />
        <Sidebar />
        <Query
          query={GET_OFFICERS_FORM}
          variables={{ id: this.props.match.params.id }}
          notifyOnNetworkStatusChange={true}
        >
          {({ loading, error, data, refetch }) => {
            if (loading) return <LoadingScreen />;
            if (error) {
              // loading data from json for development
              return <ErrorScreen message={error} retry={refetch} />;
            }
            var officersFormData = [...data.officersForm.fields];
            var officersFormDataGrouped = [];
            const size = 3;
            while (officersFormData.length > 0) {
              officersFormDataGrouped.push(officersFormData.splice(0, size));
            }
            console.log(data.officersForm.fields);
            return (
              <div>
                <QuestionCards
                  officersFormDataGrouped={officersFormDataGrouped}
                />
              </div>
            );
          }}
        </Query>
        <div className="main-content">
          <Query
            query={GET_OFFICERS_APPLICATION}
            variables={{ id: this.props.match.params.id }}
            notifyOnNetworkStatusChange={true}
          >
            {({ loading, error, data, refetch }) => {
              if (loading)
                return (
                  <div className="ad-container">
                    <LoadingScreen />
                  </div>
                );
              if (error)
                return (
                  <div className="ad-container">
                    <ErrorScreen message={error} retry={refetch} />
                  </div>
                );
              return <RightSideBar data={data.officersApplication} />;
            }}
          </Query>
        </div>
      </>
    );
  }
}

TechnicalVerification.propTypes = {
  match: PropTypes.object,
};
