import React, { useContext, useState } from 'react';
import { Dropdown } from 'react-bootstrap';
import history from '../../services/history';
import PropTypes from 'prop-types';
import { AppContext } from './../../AppContext';
import StatusReviewModal from '../StatusReviewModal';

const mapStatusToClassName = {
  'In Progress': 'inprogress',
  'In Hold': 'deemed',
  notstarted: 'notstarted',
  Forwarded: 'notstarted',
  Accepted: 'approved',
  Rejected: 'rejected',
};

function ApplicationItem(props) {
  const { permissionLevel } = useContext(AppContext);
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const arrSubItems = [
    {
      header: 'Site Review',
      time_stamp: '1Day 19Hrs 5Mins',
      date: '19 July 2020',
      status: 'Done',
      assignee: 'Kiran Kumar',
      desg: 'TPSO Officer',
    },
    {
      header: 'Technical Review',
      time_stamp: '20Days 19Hrs 5Mins',
      date: '19 July 2020',
      status: 'Done',
      assignee: 'Kiran Kumar',
      desg: 'TPSO Officer',
    },
    {
      header: 'Title Review',
      time_stamp: '28Days 19Hrs 5Mins',
      date: '19 July 2020',
      status: 'Done',
      assignee: 'Kiran Kumar',
      desg: 'TPSO Officer',
    },
  ];
  return (
    <tr className={mapStatusToClassName[props.data.status]}>
      {/* options :approved,notstarted,rejected,inhold,inprogress  */}

      <td>
        <span className="icon flag">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="13"
            height="18.076"
            viewBox="0 0 13 18.076"
          >
            <g transform="translate(-71.891)">
              <g transform="translate(71.891)">
                <path
                  className="a"
                  d="M84.891,1.2H72.95V0H71.891V18.076H72.95v-9.2H84.891L81.269,5.038Z"
                  transform="translate(-71.891)"
                />
              </g>
            </g>
          </svg>
        </span>
      </td>

      <td
        className="pl-0"
        style={{ cursor: 'pointer' }}
        onClick={(event) => {
          event.stopPropagation();
          history.push(`/application-details/${props.data.id}`);
        }}
      >
        <p>{props.data.id}</p>
        <span>{props.data.applicant.applicantName}</span>
      </td>
      <td>
        <p>{props.data.approvalFor}</p>
      </td>
      <td>
        <p>{props.data.property.plotAddress}</p>
        <span>{props.data.property.location.village}</span>
      </td>
      <td>
        <p>{props.data.property.actualPlotArea}</p>
        <span>{props.data.property.floor?.noOfFloors}</span>
      </td>
      <td className="pr-2">
        <p>09 July 2020</p>
        <div className="date-container">
          <div
            style={{ cursor: 'pointer' }}
            className="d-flex"
            onClick={handleShow}
          >
            <p>STATUS REVIEW</p>
            <span className="status-review"> </span>
          </div>
          {show ? (
            <StatusReviewModal data={arrSubItems} handleClose={handleClose} />
          ) : null}
          <p>Tech..Verification</p>
          {/* <ul>{subStateFlow(props.data.subStateLevel).map((item) => item)}</ul> */}
          <span> {props.data.subState} </span>
        </div>
      </td>
      <td>
        <div className="d-flex align-items-center">
          <button className={`btn  ${mapStatusToClassName[props.data.status]}`}>
            {props.data.status}
          </button>
          <Dropdown
            style={{ cursor: 'pointer' }}
            onClick={(event) => {
              event.stopPropagation();
              if (
                permissionLevel === 'acp-officer' ||
                permissionLevel === 'commisioner'
              ) {
                history.push(`/commissioner-verification/${props.data.id}`, {
                  pageType: props.pageType,
                });
              } else if (permissionLevel === 'technical-verification') {
                history.push(`/technical-verification/${props.data.id}`);
              }
            }}
          >
            <Dropdown.Toggle>Verify</Dropdown.Toggle>

            <Dropdown.Menu>
              <Dropdown.Item
                style={{ cursor: 'pointer' }}
                onClick={(event) => {
                  event.stopPropagation();
                  history.push(`/title-verification/${props.data.id}`);
                }}
              >
                {' '}
                Title Verification
              </Dropdown.Item>
              <Dropdown.Divider />
              <Dropdown.Item
                style={{ cursor: 'pointer' }}
                onClick={(event) => {
                  event.stopPropagation();
                  history.push(`/technical-verification/${props.data.id}`);
                }}
              >
                Technical Verification
              </Dropdown.Item>
              <Dropdown.Divider />
              <Dropdown.Item
                style={{ cursor: 'pointer' }}
                onClick={(event) => {
                  event.stopPropagation();
                  history.push(`/site-verification/${props.data.id}`);
                }}
              >
                Site Verification
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </div>
      </td>
    </tr>
  );
}

ApplicationItem.propTypes = {
  data: PropTypes.shape({
    id: PropTypes.string.isRequired,
    status: PropTypes.string,
    subState: PropTypes.string,
    approvalFor: PropTypes.string,
    applicant: PropTypes.shape({
      applicantName: PropTypes.string,
    }),
    property: PropTypes.shape({
      plotAddress: PropTypes.string,
      location: PropTypes.object,
      actualPlotArea: PropTypes.number,
      floor: PropTypes.object,
    }),
  }),
  pageType: PropTypes.string,
};

// Specifies the default values for props:
ApplicationItem.defaultProps = {
  pageType: 'post-verification',
};

export default ApplicationItem;
