import { gql } from '@apollo/client';

export const GET_OFFICERS_APPLICATION = gql`
  query OfficersApplication($id: ID!) {
    officersApplication(id: $id) {
      id
      answers {
        id
        text
        question
      }
      applicant {
        id
        applicantName
        aadhaarNumber
        mobile
        email
        contactAddress
      }
      property {
        id
        plotAddress
        geoCoordinates

        marketValue

        northPlotWidth
        southPlotWidth
        eastPlotWidth

        sroLocation

        actualPlotArea
        plotAreaAsPerDocument
        eastRoadWidth
      }
      documents {
        id
        category
        s3FilePath
      }
      # survey number
    }
  }
`;
