import React, { Component } from 'react';
import { Container, Button } from 'react-bootstrap';
import OtpInput from 'react-otp-input';
import history from '../services/history';

class ChangePasswordOTP extends Component {
  state = {
    otp: '',
    errorMsg: false,
    loading: true,
  };

  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.handleVerify = this.handleVerify.bind(this);
  }
  handleChange = (otp) => {
    this.setState({ otp });
  };

  handleVerify() {
    if (this.state.otp.length === 4) {
      history.push('/reset-password-new');
    } else {
      this.setState({ errorMsg: true });
    }
  }

  render() {
    let errorMsg;
    if (this.state.otp.length < 4) {
      errorMsg = (
        <div style={{ color: '#DC3545' }} className="fs-12 mt-1">
          Please fill the OTP
        </div>
      );
    } else {
      errorMsg = <div />;
    }

    let verifyButton;
    // Verify Otp mutation to be added
    if (this.state.loading) {
      verifyButton = (
        <Button
          variant="primary"
          type="submit"
          className="d-flex align-items-center justify-content-around"
          onClick={this.handleVerify}
        >
          <span className="text-uppercase">Verify</span>
          <img src={require('../assets/images/arrow.svg')} alt="" />
        </Button>
      );
    } else {
      verifyButton = (
        <Button variant="secondary">
          <div className="spinner-border" role="status">
            <span className="sr-only">Loading...</span>
          </div>
        </Button>
      );
    }

    return (
      <div className="change-pwd-otp-container">
        <Container>
          <div className="change-pwd-otp-content">
            <strong>OTP Verification</strong>
            <p className="fs-12">Please enter the OTP received to your email</p>
            <br />
            <p className="fs-12">One Time Password</p>
            <div>
              <OtpInput
                onChange={this.handleChange}
                value={this.state.otp}
                numInputs={4}
                isInputNum="true"
                containerStyle={{
                  width: '224px',
                  justifyContent: 'space-between',
                }}
                inputStyle={{
                  height: '50px',
                  width: '50px',
                  border: '1px solid #3F4A50',
                  borderRadius: '4px',
                }}
              />
              {errorMsg}
            </div>
            <br />
            {verifyButton}
            <br />
            <p className="fs-12">
              Didn&apos;t receive Code{' '}
              <span className="ml-3 pointer" style={{ color: '#1C9A5B' }}>
                <strong>Resend</strong>
              </span>
            </p>
          </div>
        </Container>
      </div>
    );
  }
}

export default ChangePasswordOTP;
