import React, { Component } from 'react';
import AuthLayout from '../components/layouts/AuthLayout';
import { connect } from 'react-redux';
import { Form, Button, Row } from 'react-bootstrap';
import { changePin } from '../store/auth/actions';
import PropTypes from 'prop-types';

class ChangePassword extends Component {
  state = {
    mobileNumber: '',
    password: '',
    passwordConf: '',
    otp: '',
    validated: false,
  };

  constructor(props) {
    super(props);
    this.handleChangeMobileNumber = this.handleChangeMobileNumber.bind(this);
    this.handleChangePassword = this.handleChangePassword.bind(this);
    this.handleChangePasswordConf = this.handleChangePasswordConf.bind(this);
    this.handleChangeOtp = this.handleChangeOtp.bind(this);

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChangeMobileNumber(event) {
    this.setState({
      mobileNumber: event.target.value,
    });
  }

  handleChangePassword(event) {
    this.setState({
      password: event.target.value,
    });
  }

  handleChangePasswordConf(event) {
    this.setState({
      passwordConf: event.target.value,
    });
  }

  handleChangeOtp(event) {
    this.setState({
      otp: event.target.value,
    });
  }

  handleSubmit(e) {
    e.preventDefault();
    const form = e.currentTarget;
    if (form.checkValidity() === false) {
      this.setState({ validated: true });
    } else {
      let inputData = {
        officer: {
          mobile: this.state.mobileNumber,
          password: this.state.password,
          password_confirmation: this.state.passwordConf,
          otp_secret_key: this.state.otp,
        },
      };
      this.props.changePin(inputData);
    }
  }

  render() {
    let submitButton;
    if (!this.props.showSpinner) {
      submitButton = (
        <Button variant="primary" type="submit">
          Change Password{' '}
        </Button>
      );
    } else {
      submitButton = (
        <Button variant="primary">
          <div className="spinner-border" role="status">
            <span className="sr-only"> Loading... </span>{' '}
          </div>{' '}
        </Button>
      );
    }

    return (
      <AuthLayout>
        <h2>Officer&apos;s Password Change</h2>
        <Form
          noValidate
          validated={this.state.validated}
          onSubmit={this.handleSubmit}
        >
          <Form.Group controlId="formBasicNumber">
            <Form.Label> Mobile Number </Form.Label>{' '}
            <Form.Control
              required
              type="number"
              placeholder="Enter mobile number"
              value={this.state.mobileNumber}
              onChange={this.handleChangeMobileNumber}
            />
            <Form.Control.Feedback type="invalid">
              Please choose a mobile number
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group controlId="formBasicPassword">
            <Form.Label> Enter Password </Form.Label>{' '}
            <Form.Control
              required
              type="password"
              placeholder="Enter Enter Password"
              value={this.state.password}
              onChange={this.handleChangePassword}
            />
            <Form.Control.Feedback type="invalid">
              Please choose a Password
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group controlId="formBasicPasswordConf">
            <Form.Label> Renter Password </Form.Label>{' '}
            <Form.Control
              required
              type="password"
              placeholder="Re enter Password"
              value={this.state.passwordConf}
              onChange={this.handleChangePasswordConf}
            />
            <Form.Control.Feedback type="invalid">
              Please enter the same password
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group controlId="formBasicOtp">
            <Form.Label> Enter OTP </Form.Label>{' '}
            <Form.Control
              required
              type="number"
              placeholder="Enter otp "
              value={this.state.otp}
              onChange={this.handleChangeOtp}
            />
            <Form.Control.Feedback type="invalid">
              Please enter the OTP
            </Form.Control.Feedback>
          </Form.Group>{' '}
          <Row> {submitButton} </Row>{' '}
        </Form>{' '}
      </AuthLayout>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    showSpinner: state.util.showSpinner,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    changePin: (data) => {
      dispatch(changePin(data));
    },
  };
};

ChangePassword.propTypes = {
  changePin: PropTypes.func,
  showSpinner: PropTypes.bool,
};

export default connect(mapStateToProps, mapDispatchToProps)(ChangePassword);
