import React from 'react';
import PropTypes from 'prop-types';
import { Route, Redirect } from 'react-router-dom';
import { useQuery } from '@apollo/client';
import { GET_OFFICER_LOGIN_STATUS } from '../graphql/query/officerLoginStatus';

export default function RouteWrapper({
  component: Component,
  isPrivate,
  ...rest
}) {
  // eslint-disable-next-line no-undef
  // const signed = !!localStorage.getItem('accessToken');
  var signed = false;
  const { data, loading, error } = useQuery(GET_OFFICER_LOGIN_STATUS);
  if (loading) return <a>loading..</a>;
  if (error) return <a>error resolving login state</a>;
  signed = data.isLoggedIn;

  const routeUrl = rest?.computedMatch?.path;

  if (routeUrl === '/reset-password' || routeUrl === '/change-password') {
    return <Route {...rest} component={Component} />;
  }

  /**
   * Redirect user to SignIn page if he tries to access a private route
   * without authentication.
   */
  if (isPrivate && !signed) {
    return <Redirect to="/" />;
  }

  /**
   * Redirect user to Main page if he tries to access a non private route
   * (SignIn or SignUp) after being authenticated.
   */
  if (!isPrivate && signed) {
    return <Redirect to="/post-verification" />;
  }

  /**
   * If not included on both previous cases, redirect user to the desired route.
   */
  return <Route {...rest} component={Component} />;
}

RouteWrapper.propTypes = {
  isPrivate: PropTypes.bool,
  component: PropTypes.oneOfType([PropTypes.element, PropTypes.func])
    .isRequired,
};
RouteWrapper.defaultProps = {
  isPrivate: false,
};
