import React, { useState } from 'react';
import PropTypes from 'prop-types';
import './index.scss';
import { Card, Form, Carousel } from 'react-bootstrap';
import history from '../../services/history';
import { useParams } from 'react-router';
import '../VerificationCards/index.scss';

const QUESTIONS_TYPE = {
  CHECKBOX: 'CHECKBOX', // we were considering radio buttons as a checkbox for now
  TEXT_LONG: 'TEXT_LONG',
  TEXT_SHORT: 'TEXT_SHORT',
};

function QuestionCards({ officersFormDataGrouped }) {
  const { id } = useParams();
  const [carousalIndex, setCarousalIndex] = useState(0);
  const [questionsTracked, setQuestionsTracked] = useState([]);
  const [remarksView, setRemarksView] = useState(true);

  const handleSelect = (selectedIndex, e) => {
    setCarousalIndex(selectedIndex);
  };
  const nextSlide = () => {
    let duplicateIndex = carousalIndex + 1;
    if (duplicateIndex === officersFormDataGrouped.length) {
      console.log('Integrate API on Recommend');
      duplicateIndex = duplicateIndex - 1;
    }
    return duplicateIndex;
  };
  const prevSlide = () => {
    let duplicateIndex = carousalIndex - 1;
    if (duplicateIndex === -1) {
      duplicateIndex = officersFormDataGrouped.length - 1;
    }
    return duplicateIndex;
  };
  const trackQuestion = (cardIndex, questionIndex) => {
    const key = cardIndex + '' + questionIndex;
    if (!questionsTracked.filter((item) => item === key).length) {
      setQuestionsTracked([...questionsTracked, key]);
    }
  };
  const handleRemarksViewToggle = () => {
    setRemarksView(!remarksView);
  };

  return (
    <>
      <div className="main-content">
        <div className="tv-container ">
          <div className="tv-header">
            <ol className="breadcrumbs">
              <li
                className="active"
                style={{ cursor: 'pointer' }}
                onClick={(e) => {
                  history.push('/post-verification');
                }}
              >
                <a href="#!">Post Verification</a>
              </li>
              <li>
                <a href="#!">{id}</a>
              </li>
            </ol>
          </div>
          <div className="tv-content">
            <div style={{ float: 'right' }}>
              <ul className="stepper-dots">
                {officersFormDataGrouped.map((item, index) => (
                  <a href="#!" key={index}>
                    <li
                      className={carousalIndex === index ? 'active' : ''}
                      onClick={(e) => handleSelect(index, e)}
                    />
                  </a>
                ))}
              </ul>
            </div>

            <h6>Technical Verification</h6>
            <Carousel
              activeIndex={carousalIndex}
              controls={false}
              onSelect={handleSelect}
              interval={100000}
            >
              {officersFormDataGrouped.map((cardData, cardIndex) => {
                return (
                  <Carousel.Item key={cardIndex}>
                    <Card key={cardIndex}>
                      <Card.Body>
                        {cardData.map((question, questionIndex) => {
                          return (
                            <div className="tv-questions" key={questionIndex}>
                              {question.fieldType && (
                                <>
                                  <p>{question.title}</p>
                                  <Form>
                                    {question.fieldType ===
                                    QUESTIONS_TYPE.CHECKBOX ? (
                                      <div className="radio-group">
                                        <label
                                          onClick={() =>
                                            trackQuestion(
                                              cardIndex,
                                              questionIndex
                                            )
                                          }
                                        >
                                          <input type="radio" name="answers" />
                                          Yes
                                        </label>
                                        <label
                                          onClick={() =>
                                            trackQuestion(
                                              cardIndex,
                                              questionIndex
                                            )
                                          }
                                        >
                                          <input type="radio" name="answers" />
                                          No
                                        </label>
                                      </div>
                                    ) : question.fieldType ===
                                      QUESTIONS_TYPE.TEXT_LONG ? (
                                      <div className="input-group">
                                        <label
                                          className="w-100"
                                          onClick={() =>
                                            trackQuestion(
                                              cardIndex,
                                              questionIndex
                                            )
                                          }
                                        >
                                          <textarea
                                            className="w-100"
                                            name="long_text"
                                          />
                                        </label>
                                      </div>
                                    ) : question.fieldType ===
                                      QUESTIONS_TYPE.TEXT_SHORT ? (
                                      <div className="input-group">
                                        <label
                                          className="w-100"
                                          onClick={() =>
                                            trackQuestion(
                                              cardIndex,
                                              questionIndex
                                            )
                                          }
                                        >
                                          <textarea
                                            className="w-100"
                                            name="short_text"
                                          />
                                        </label>
                                      </div>
                                    ) : (
                                      ''
                                    )}
                                  </Form>
                                  {questionsTracked.filter(
                                    (item) =>
                                      item === cardIndex + '' + questionIndex
                                  ).length === 1 &&
                                    question.children.map(
                                      (childrenQuestion, childrenIndex) => {
                                        return (
                                          <div
                                            className="tv-questions"
                                            key={childrenIndex}
                                          >
                                            {childrenQuestion.fieldType && (
                                              <>
                                                <p>{childrenQuestion.title}</p>
                                                <Form>
                                                  {childrenQuestion.fieldType ===
                                                  QUESTIONS_TYPE.CHECKBOX ? (
                                                    <div className="radio-group">
                                                      <label>
                                                        <input
                                                          type="radio"
                                                          name="answers"
                                                        />
                                                        Yes
                                                      </label>
                                                      <label>
                                                        <input
                                                          type="radio"
                                                          name="answers"
                                                        />
                                                        No
                                                      </label>
                                                    </div>
                                                  ) : childrenQuestion.fieldType ===
                                                    QUESTIONS_TYPE.TEXT_LONG ? (
                                                    <div className="input-group">
                                                      <label className="w-100">
                                                        <textarea
                                                          className="w-100"
                                                          name="short_text"
                                                        />
                                                      </label>
                                                    </div>
                                                  ) : childrenQuestion.fieldType ===
                                                    QUESTIONS_TYPE.TEXT_SHORT ? (
                                                    <div className="input-group">
                                                      <label className="w-100">
                                                        <textarea
                                                          className="w-100"
                                                          name="short_text"
                                                        />
                                                      </label>
                                                    </div>
                                                  ) : (
                                                    ''
                                                  )}
                                                </Form>
                                              </>
                                            )}
                                          </div>
                                        );
                                      }
                                    )}
                                </>
                              )}
                            </div>
                          );
                        })}
                        <div>
                          {carousalIndex ===
                          officersFormDataGrouped.length - 1 ? (
                            remarksView ? (
                              <div>
                                <div className="bottom-section">
                                  <div>
                                    <h6
                                      type="button"
                                      onClick={() => handleRemarksViewToggle()}
                                    >
                                      Add Remarks
                                    </h6>
                                    <span>Click to add your remarks</span>
                                  </div>
                                </div>
                                <div className="border-btm">
                                  <hr />
                                  <span>Choose an action and proceed</span>
                                </div>
                              </div>
                            ) : (
                              <div className="bottom-section1">
                                <Form>
                                  <Form.Group controlId="exampleForm.ControlTextarea1">
                                    <Form.Label className="form-Label">
                                      Add Remarks
                                    </Form.Label>
                                    <img
                                      type="button"
                                      onClick={() => handleRemarksViewToggle()}
                                      src={require('../../assets/images/success.svg')}
                                    />
                                    <Form.Control
                                      as="textarea"
                                      rows="3"
                                      placeholder="Text here"
                                    />
                                  </Form.Group>
                                </Form>
                                <div className="btn-recommend-notrecommend">
                                  <button className="btn btn-light btn-notrecmnd">
                                    <img
                                      src={require('../../assets/images/successLightGreen.svg')}
                                    />
                                    NOT RECOMMEND{' '}
                                  </button>
                                  <button className="btn btn-light btn-recmnd">
                                    <img
                                      src={require('../../assets/images/successDarkGreen.svg')}
                                    />
                                    RECOMMEND{' '}
                                  </button>
                                </div>
                              </div>
                            )
                          ) : (
                            <></>
                          )}
                        </div>

                        <div className="btn-container">
                          <button
                            className={
                              carousalIndex === 0 ? 'd-none' : 'btn btn-light '
                            }
                            onClick={() => handleSelect(prevSlide())}
                          >
                            <img
                              src={require('../../assets/images/arrowBackwards.svg')}
                              alt="previous"
                            />
                            BACK
                          </button>
                          <button
                            className={
                              carousalIndex === 0
                                ? 'btn btn-success btn-firstPage'
                                : 'btn btn-success '
                            }
                            onClick={() => {
                              if (
                                carousalIndex ===
                                officersFormDataGrouped.length - 1
                              ) {
                                history.push('/scrutiny');
                              } else {
                                handleSelect(nextSlide());
                              }
                            }}
                          >
                            {carousalIndex ===
                            officersFormDataGrouped.length - 1
                              ? 'SUBMIT'
                              : 'NEXT'}
                            <img
                              src={require('../../assets/images/arrow.svg')}
                              alt="next"
                            />
                          </button>
                        </div>
                      </Card.Body>
                    </Card>
                  </Carousel.Item>
                );
              })}
            </Carousel>
          </div>
        </div>
      </div>
    </>
  );
}

QuestionCards.propTypes = {
  officersFormDataGrouped: PropTypes.array,
};

export default QuestionCards;
