import { gql } from '@apollo/client';

export const GET_OFFICERS_APPLICATIONS = gql`
  query {
    officersApplications {
      applicant {
        applicantName
      }
      approvalFor
      id

      property {
        actualPlotArea
        plotAddress
        # following is commented as it causes graphql error
        #  floor {
        #    noOfFloors
        #  }
        location {
          village
        }
      }
      status
    }
  }
`;
