import * as AuthEffect from './effects';
import { toggleSpinner } from '../common/actions';
import history from '../../services/history';
import * as AuthActionTypes from './actionTypes';

export const setLoginState = (loginData) => {
  return {
    type: AuthActionTypes.SET_LOGIN_STATE,
    payload: loginData,
  };
};

export const setLogoutState = () => {
  return {
    type: AuthActionTypes.SET_LOGOUT_STATE,
  };
};

export const logoutUser = () => {
  return {
    type: AuthActionTypes.SET_LOGOUT_STATE,
  };
};

// renamed from login as Login component prop too had login
export const loginUser = (loginInput) => {
  return (dispatch) => {
    dispatch(toggleSpinner(true));
    return AuthEffect.login(loginInput)
      .then((data) => {
        dispatch(toggleSpinner(false));
        dispatch(setLoginState(data));
        history.push('/post-verification');
      })
      .catch((err) => {
        dispatch(toggleSpinner(false));
        console.log(err);
      });
  };
};

