import { gql } from '@apollo/client';

export const GET_OFFICERS_FORM = gql`
  query getQuestions($id: ID!) {
    # rename "9" to $id later
    officersForm(id: $id) {
      id
      title
      fields {
        id
        title
        fieldType
        order
        children {
          id
          title
          fieldType
        }
      }
    }
  }
`;
